<template>
  <div class="dialog dialog20">
    <div ref="dialog20" class="dialogs" @click.native="moveNext">
      <template v-for="(item, index) in dialogs.dialog20">
        <div v-if="dialogCurrent === index && item.type === 'dialog'" :key="index" class="animated fadeIn" :class="item.class + ' dialog' + (index + 1)">
          <span>{{item.content}}</span>
        </div>
        <div v-else-if="dialogCurrent === index && item.type === 'action'" :key="index" :class="item.class + ' dialog' + (index + 1)">
          <div class="animated infinite zoomIn">
            <img src="../assets/yushan.png" alt="">
          </div>
        </div>
      </template>
    </div>
  </div>
</template>

<script>
import dialogs from '../dialogs'

export default {
  name: 'HelloWorld',
  props: ['dialogCurrent'],
  data () {
    return {
      dialogs,
    }
  },
  watch: {
    dialogCurrent (val) {
      if (val > dialogs.dialog20.length - 2) {
        this.$emit('move-next')
      }
    }
  },
  methods: {
    moveNext(e) {
      e.preventDefault()
      console.log('moveNext')
      this.$emit('move-next')
      // this.currentDialogs++
    }
  }
}
</script>

<style lang="less" scoped>
.dialog20 {
  .dialog1 {
    width: 1.7rem;
    top: 0.88rem;
    left: .1rem;
  }
}
</style>

