<template>
  <div class="dialog dialog-21 dialog-dom">
    <div ref="dialog4" class="dialogs" @click.native="moveNext">
      <template v-for="(item, index) in dialogs.dialog21">
        <div v-if="dialogCurrent === index && item.type === 'dialog'" :key="index" class="animated fadeIn" :class="item.class + ' dialog' + (index + 1)">
          <span>{{item.content}}</span>
        </div>
        <div v-else-if="dialogCurrent === index && item.type === 'action'" :key="index" :class="item.class + ' dialog' + (index + 1)">
          <div class="animated infinite zoomIn">
            <img src="../assets/yushan.png" alt="">
          </div>
        </div>
      </template>
    </div>
  </div>
</template>

<script>
import dialogs from '../dialogs'

export default {
  name: 'HelloWorld',
  props: ['dialogCurrent'],
  data () {
    return {
      dialogs,
    }
  },
  watch: {
    dialogCurrent (val) {
      console.log('dialogCurrentWatch', val, dialogs.dialog21.length )
      if (val > dialogs.dialog21.length - 1) {
        this.$emit('move-next')
      }
    }
  },
  methods: {
    moveNext(e) {
      e.preventDefault()
      console.log('moveNext')
      this.$emit('move-next')
    }
  }
}
</script>

